.pms-firstcont{
    padding-left: 5rem;
}
.rd{
    padding-left: 5rem !important;
}
.cb{
    padding-right: 5rem !important;
}
.imag{
    padding: 1rem;
}
.industry{
    display: flex;
    justify-content: space-around;
    box-shadow: 0 0 2px 2px whitesmoke;
}
.db{
    padding-bottom: 5rem;
    background-color: white !important;
}
.shadoe{
    box-shadow: 0 0 2px 2px whitesmoke;
}
.industry{
    padding-top: 5rem;
