}
.fsl{
    margin-right: 0px !important;
}
.pricing{
    display: flex;
    justify-content: space-around;
}
.purebusiness{
    display: flex;
    justify-content: space-around;
}
.enterprise{
    width: 250px;
}
.enterprise h4{
    color: #3BCBF9;
}
.greene{
    color: #2BDE00;
}
.lst{
    list-style-type: none !important;
    margin-left: 2.5em;
    padding-left: 0px;
}
.beam{
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
}
.pms-fl ul, .pms-sl ul{
    list-style-type: none !important;
}
